<template>
  <div>
    <a-layout>
      <a-layout-header style="padding: 0; height: 50px">
        <!-- 控制按钮 -->
        <a-menu mode="horizontal" style="color: black; height: 100%">
          <a-sub-menu>
            <span slot="title" class="submenu-title-wrapper">
              <a-icon type="file" />文件
            </span>
            <a-menu-item-group>
              <a-menu-item key="setting:1">
                <a type="primary" @click="openFile">打开</a>
              </a-menu-item>
              <a-menu-item key="setting:2">
                <a type="primary" @click="saveFile">保存</a>
              </a-menu-item>
            </a-menu-item-group>
          </a-sub-menu>

          <a-menu-item>
            <a-input
              v-model="projectName"
              placeholder="请输入项目名称"
              style="width: 200px"
            ></a-input>
            <a-select
              v-model="language"
              style="width: 150px"
              class="code-mode-select"
              @change="changeLanguage"
              placeholder="请选择语言"
            >
              <a-select-option
                v-for="mode in supportLanguages"
                :key="mode.key"
                :value="mode.key"
              >
                {{ mode.label }}
              </a-select-option>
            </a-select>
          </a-menu-item>
          <a-menu-item>
            <a-button type="primary" @click="submitCode">提交</a-button>
          </a-menu-item>

          <a-menu-item>
            <a-button
              type="primary"
              icon="play-circle"
              @click="runit"
              :loading="runing"
              >运行</a-button
            >
          </a-menu-item>
          <a-menu-item>
            <a-button type="primary" icon="delete" @click="clear"
              >清空</a-button
            >
          </a-menu-item>
          <!-- <a-menu-item style="float: right">
            <div class="right-menu">
              <a-input v-show="courseName!=''" :value="courseName" disabled=""></a-input>
              <img :src="avatar" alt="">
              <span>{{username}}</span>
            </div>
          </a-menu-item> -->
        </a-menu>
        <input type="file" id="fileinput" style="display: none" />
      </a-layout-header>
      <a-layout>
        <a-layout-content>
          <a-form-item>
            <JCodeEditor
              v-if="editorType == 'codemirror'"
              language="python"
              v-model="code"
              :fullScreen="true"
              placeholder="请输入代码"
              ref="codeEditor"
            />
            <AceEditor
              v-if="editorType == 'ace'"
              language="python"
              v-model="code"
              :height="'calc(100vh - 74px)'"
              ref="codeEditor"
            />
          </a-form-item>
        </a-layout-content>
        <a-layout-sider :collapsible="false" :width="500" :theme="'light'">
          <!-- 画布 -->
          <div
            id="mycanvas"
            v-show="language == 'pygame' || language == 'turtle'"
          ></div>
          <div>
            <p>运行输出：</p>
            <pre id="output">{{ out }}</pre>
          </div>

          <div v-show="language !== 'pygame' && language !== 'turtle'">
            <p>输入参数：</p>
            <a-textarea v-model="stdin"></a-textarea>
          </div>
        </a-layout-sider>
      </a-layout>
      <!-- <a-layout-footer></a-layout-footer> -->
    </a-layout>
  </div>
</template>

<script>
import { saveAs } from "file-saver";
import JCodeEditor from "./JCodeEditor";
import AceEditor from "./AceEditor";
import "../python/skulpt.min.js";
import "../python/skulpt-stdlib.js";

import axios from "axios";

const Sk = window.Sk;

export default {
  name: "TeachingEditor",
  components: {
    JCodeEditor,
    AceEditor,
  },
  props: {
    editorType: {
      type: String,
      default: "ace", //ace codemirror
    },
  },
  data() {
    return {
      language: "",
      supportLanguages: [
        { key: "cpp", label: "C++" },
        { key: "c", label: "C语言" },
        { key: "python", label: "Python" },
        { key: "java", label: "Java" },
        { key: "turtle", label: "Python Turtle" },
      ],
      languageSuffix: {
        cpp: ".cpp",
        c: ".c",
        python: ".py",
        turtle: ".py",
        java: ".java",
        php: ".php",
      },
      suffixLanguage: {
        cpp: "cpp",
        c: "c",
        py: "python",
        java: "java",
        php: "php",
      },
      languageApi: {
        cpp: "/api/teaching/onlineCode/cpp",
        c: "/api/teaching/onlineCode/c",
        python: "/api/teaching/onlineCode/python",
        java: "/api/teaching/onlineCode/java",
        php: "/api/teaching/onlineCode/php",
      },
      projectName: "",
      code: "",
      out: "",
      stdin: "",
      runing: false,
      courseName: "自由创作",
      username: "未登录",
      avatar: "./static/avatar.png"
    };
  },
  mounted() {},
  created() {
    var that = this;

    //载入网络项目
    var url = this.urlParam("url");
    that.autoLoad(url);

    //兼容载入项目事件
    window.document.addEventListener("loadPorject", function (e) {
      console.log("load project:");
      console.log(e.detail);
      that.projectName = e.detail.projectName;
      that.language = e.detail.language;
      that.autoLoad(e.detail.url);
      that.courseName = e.detail.courseName;
    });
    window.document.addEventListener("setUserInfo", function(e){
      console.log("set user info:");
      console.log(e.detail);
      that.username = e.detail.username;
      if(e.detail.avatar)
        that.avatar = e.detail.avatar;
    });
  },
  methods: {
    autoLoad(url) {
      let lang = this.urlParam("lang");
      //如果没有设置语言，则自动从后缀名判断语言
      if (!lang) {
        if (url) {
          this.language = this.suffixLanguage[this.getSuffixByUrl(url)];
        }
      } else {
        this.language = lang;
      }
      if (url) {
        //加载网络文件
        this.downloadFile(url);
      } else {
        //加载默认项目，判断是否设置了语言，无则默认pygame
        switch (lang) {
          case "cpp":
            this.downloadFile("./static/default.cpp");
            break;
          case "c":
            this.downloadFile("./static/default.c");
            break;
          case "python":
            this.downloadFile("./static/default.py");
            break;
          case "java":
            this.downloadFile("./static/default.java");
            break;
          case "turtle":
            this.downloadFile("./static/defaultTurtle.py");
            break;
          default:
            this.downloadFile("./static/defaultTurtle.py");
            this.language = "turtle";
        }
      }
      console.log("run code with " + this.language);
    },
    runit() {
      if (!this.languageSuffix.hasOwnProperty(this.language)) {
        this.$message.error("不支持该语言");
      }
      if (["pygame", "turtle"].includes(this.language)) {
        this.runWithSk();
      } else {
        this.runWithApi();
      }
    },
    runWithApi() {
      var that = this;
      that.runing = true;
      this.clear();
      axios
        .post(this.languageApi[this.language], {
          files: [
            {
              name:
                (this.projectName ? this.projectName : "Main") +
                this.languageSuffix[this.language],
              content: this.getCode(),
            },
          ],
          stdin: that.stdin,
        })
        .then((res) => {
          if (res.data.error != "") {
            this.$notification.open({
              message: res.data.error,
              description: res.data.message || res.data.stderr,
              icon: <a-icon type="sad" style="color: #108ee9" />,
            });
          }
          that.terminalOut(res.data.stdout);
          that.runing = false;
        });
    },
    runWithSk() {
      var that = this;
      this.clear();
      Sk.pre = "output";
      Sk.configure({ output: that.terminalOut, read: that.builtinRead });
      (Sk.TurtleGraphics || (Sk.TurtleGraphics = {})).target = "mycanvas";
      var myPromise = Sk.misceval.asyncToPromise(function () {
        return Sk.importMainWithBody("<stdin>", false, that.getCode(), true);
      });
      myPromise.then(
        function (mod) {
          that.$message.success("执行完毕");
        },
        function (err) {
          that.$message.error(err.toString());
        }
      );
    },
    changeLanguage(lang) {},
    downloadFile(url) {
      var that = this;
      this.projectName =
        this.projectName == "" ? this.getNameByUrl(url) : this.projectName;
      axios.get(url).then((res) => {
        that.setCode(res.data);
      });
    },
    openFile() {
      var that = this;
      var fileInput = document.getElementById("fileinput");
      fileInput.addEventListener("change", function (e) {
        that.projectName = that.getNameByUrl(fileInput.value);
        that.language =
          that.suffixLanguage[that.getSuffixByUrl(fileInput.value)];
        const reader = new FileReader();
        const thisFileInput = e.target;
        reader.onload = function () {
          that.setCode(this.result);
          that.code = this.result;
        };
        reader.readAsText(thisFileInput.files[0]);
      });
      fileInput.click();
    },
    saveFile() {
      console.log("save file " + this.projectName);
      if( this.projectName == ""){
        that.$message.info("没有文件名");
        return
      }
      if (this.getCode() == "") {
        that.$message.info("没有代码可以保存");
        return
      }
      var n = new File([this.getCode()], 
        this.projectName, 
        {type: "text/plain;charset=utf-8",});
      saveAs(n);
    },
    submitCode() {
      if (this.getCode() == "") {
        that.$message.info("没有代码可以提交");
        return
      }
      if (window.submitCode) {
        window.submitCode(this.projectName, this.language, this.code);
      }
    },
    setCode(code) {
      this.code = code;
      this.$refs.codeEditor.setCodeContent(code);
    },
    getCode() {
      this.code = this.$refs.codeEditor.getCodeContent();
      return this.code;
    },
    clear() {
      document.getElementById("output").innerHTML = "";
      document.getElementById("mycanvas").innerHTML = "";
    },
    terminalOut(v) {
      var mypre = document.getElementById("output");
      mypre.innerHTML = mypre.innerHTML + v;
    },
    builtinRead(n) {
      if (
        Sk.builtinFiles === undefined ||
        Sk.builtinFiles.files[n] === undefined
      )
        throw `File not found: '${n}'`;
      return Sk.builtinFiles.files[n];
    },
    getSuffixByUrl(url) {
      let part = url.split(".");
      return part[part.length - 1];
    },
    getNameByUrl(name) {
      if (name.includes("\\")) {
        let part = name.split("\\");
        return part[part.length - 1];
      } else if (name.includes("/")) {
        let part = name.split("/");
        return part[part.length - 1];
      }
       else {
        return name;
      }
    },
    urlParam(name) {
      var results = new RegExp("[\\?&]" + name + "=([^&#]*)").exec(
        window.location.href
      );
      if (!results) {
        return 0;
      }
      return results[1] || 0;
    },
  },
};
</script>

<style lang="less">
.CodeMirror {
  /* Set height, width, borders, and global font properties here */
  font-family: monospace;
  height: calc(100vh - 74px) !important;
  color: black;
  direction: ltr;
}
#output {
  border: 1px solid black;
  height: 40vh;
  width: 100%;
  padding: 10px;
  margin: 10px 0;
}

#mycanvas {
  border: 1px solid black;
  height: 40vh;
  width: 100%;
}
.control {
  margin: 10px;
  button {
    margin: 10px 0;
  }
}
.ant-layout-header {
  background: #dfefff !important;
}
.ant-layout-sider {
  padding: 10px;
}
.right-menu {
  // float: right;
  padding: 5px 10px;
  input{
    width: 200px;
    margin-right: 20px;
  }
  img{
    height: 30px;
    width: auto;
  }
}
</style>


